.user-account {
	.memory-form {
		&__title-block {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-bottom: 10px; }
		&__title_bordered {
			@include sm {
				border-bottom: 0;
				flex-wrap: wrap;
				margin-bottom: 0; } }
		&__title {
			margin-right: 10px; }
		&__btn {
			font-weight: 600; } }
	.select {
		width: 100%;
		min-width: 105px;
		@include md {
			margin-bottom: 20px; }
		@include sm {
			margin-bottom: 0; } }
	.select .select2-selection--single .select2-selection__rendered {
		padding-left: 17px;
		padding-top: 6px;
		padding-bottom: 6px; }
	.select .select2-selection--single {
		height: 45px;
		background-color: #f1ede8; }
	.select .select2-selection--single .select2-selection__arrow {
		height: 42px;
		width: 40px; }
	.select2-container--default .select2-selection--multiple,
	.select2-container--default .select2-selection--single {
		border-radius: 0; }
	.person-info__general {
		border-bottom: 1px solid #eee;
		padding-bottom: 10px;
		margin-bottom: 10px; }
	.post-form {
		margin-bottom: 27px;
		@include sm {
			margin-bottom: 0; } }
	.form-radio {
		display: flex;
		align-items: center;
		margin-top: 12px;
		padding-bottom: 40px;
		@include sm {
			margin-top: 0;
			flex-wrap: wrap;
			padding-bottom: 0; }
		&__radio {
			display: flex;
			align-items: center;
			margin-bottom: 0;
			@include sm {
				margin-bottom: 10px; }
			&:not(:last-child) {
				margin-right: 45px;
				@include sm {
					margin-right: 35px; } }
			input {
				cursor: pointer; } }
		.social__link {
			margin-right: 8px;
			cursor: pointer; } } }
