.funeral {
    &__head {
        margin-bottom: 60px;
        @include sm {
            margin-bottom: 20px; }
        .section-head {
            max-width: 970px; } }
    &__text {
        margin-bottom: 60px;
        p {
            max-width: 970px;
            text-align: center;
            margin: 0 auto; } }
    &__map {
        margin-bottom: 70px; }
    &__tile {
        margin-bottom: 60px; }
    &__item {
        margin-bottom: 10px;
        .product-reviews__details {
            visibility: visible;
            opacity: 1;
            font-weight: bold;
            span {
                color: $brown_orange; } } }
    &__selects {
        margin-bottom: 30px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        .select {
            &:not(:last-child) {
                margin-right: 15px; } }
        @include sm {
            flex-direction: column;
            .select {
                margin-bottom: 20px;
                &:not(:last-child) {
                    margin-right: 0; } } } }
    &__list {
        margin-bottom: 50px;
        .companies-item__type {
            order: 1;
            @include sm {
                order: 4; } }
        .companies-item__descr {
            order: 2;
            @include sm {
                order: 5; } }
        .btn {
            @include sm {
                order: 6; } } }
    .bottom-text {
        p {
            margin-bottom: 30px; } } }
