//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


//START Tahoma
@font-face {
	@include fontdefault(normal, 400, 'Tahoma');
	@include fontpath('Tahoma', 'Tahoma', 'Tahoma'); }

@font-face {
	@include fontdefault(normal, 700, 'Tahoma');
	@include fontpath('Tahoma Bold', 'Tahoma-Bold', 'Tahoma-Bold'); }

