.agencies {
	padding: 65px 0 75px;
	@include sm {
		padding: 20px 0 15px; }
	&__row {
		&:not(:last-child) {
			margin-bottom: 25px;
			@include md {
				margin-bottom: 10px; } } }
	&__col {
		@include md {
			&:not(:last-child) {
				margin-bottom: 10px; } } } }
.agency {
	text-align: left;
	border: 1px solid #f3eee8;
	transition: box-shadow .4s;
	&:hover {
		box-shadow: 1.5px 2.6px 7px rgba(0, 0, 0, 0.18); }
	&__head {
		padding: 13px 23px 11px 30px;
		border-bottom: 1px solid #f3eee8;
		@include clr;
		@include sm {
			padding: 6px 10px; } }
	&__logo {
		float: left;
		max-width: 130px;
		@include sm {
			max-width: 70px; }
		&_md {
			@include sm {
				max-width: 130px; } }
		img {
			max-width: 100%;
			max-height: 56px;
			display: block; } }
	&__rating,
	&__link {
		float: right; }
	&__rating {
		margin-top: 20px;
		@include sm {
			margin-top: 4px; }
		.ratings {
			@include sm {
				padding: 0; } } }
	&__stars {
		float: left;
		margin-top: 8px;
		width: 100%; }
	&__link {
		margin-top: 15px;
		@include sm {
			margin-top: 0; }
		&:not(:first-child) {
			margin-left: 20px;
			@include sm {
				margin-left: 9px; } } }
	&__content {
		padding: 17px 15px 15px 30px;
		@include sm {
			padding: 10px 20px; } }
	&__title {
		color: #65594d;
		font-family: $cormorant_infant;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.2; }
	&__contacts {
		margin-top: 20px;
		@include sm {
			margin-top: 10px; } }
	&__table {
		padding: 23px 30px;
		width: 100%;
		table-layout: fixed;
		display: table;
		@include md {
			padding: 10px;
			column-count: 2; } }
	&__table-cell {
		width: 33.3%;
		vertical-align: middle;
		display: table-cell;
		@include clr;
		@include md {
			width: 50%;
			font-size: 12px;
			vertical-align: top;
			display: inline-block; }
		&:first-child {
			@include md {
				height: min-content; } }
		&:not(:last-child) {
			padding-right: 10px;
			@include md {
				padding-right: 0; } }
		&_mob {
			@include md {
				margin-left: 50%; } }
		&_lg {
			width: 66.7%; } }
	&__row {
		width: 100%;
		table-layout: fixed;
		display: table; }
	&__top {
		margin-bottom: 20px;
		display: flex;
		justify-content: space-between; } }
