@import url("https://fonts.googleapis.com/css?family=Crimson+Text:400,400i,600,600i,700,700i|Oxygen:300,400,700");

/* =============================
   1. RESET STYLES 
================================ */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after {
	content: '';
	content: none;
}

q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* =============================
   2. GLOBAL STYLES 
================================ */

body {
	background: #fff;
	color: #65594d;
	font-family: "Tahoma","Open Sans",sans-serif;
	font-size: 15px;
	font-weight: 400;
	line-height: 1.6;
}

a, a:hover {
	color: #65594d;
	text-decoration: none !important;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

button {
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

b, strong {
	font-weight: bold !important;
}

div, button, input, textarea, select {
	outline: none !important;
}

ul, li, ol {
	list-style: none;
	margin: 0;
	padding: 0;
}

.no-padding {
	padding: 0;
}

h1 h2, h3, h4, h5, h6 {
	font-family: 'CormorantInfant', 'Open Sans', sans-serif;
	color: #65594d;
}

h2, .h2 {
	font-size: 40px;
	margin: 0 0 5px;
}

h3, .h3 {
	font-size: 24px;
	margin: 0 0 5px;
}

.video-wrap {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
}

.video-wrap iframe,
.video-wrap object,
.video-wrap embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	border: none;
}

.container {
	width: 100% !important;
	max-width: 1200px !important;
	margin: 0 auto;
}

.wrapper {
	width: 100%;
	position: relative;
	overflow: hidden;
}

.btn {
	border: none;
}

.btn-primary {
	background-color: #65594d;
	color: #c3b5a7;
	padding: 11px 25px;
	border-radius: 5px;
	font-size: 14px;
}

.btn-primary:hover {
	background-color: #c3b5a7;
	color: #fff;
}

.btn-default {
	background-color: #e6ddd0;
	color: #65594d;
	padding: 11px 25px;
	border-radius: 5px;
	font-size: 14px;
}

.btn-default:hover {
	background-color: #65594d;
	color: #c3b5a7;
}

