/* =============================
   6. FAQ
================================ */

.accordion .panel {
	margin-bottom: 0;
	padding-bottom: 5px;
	border-radius: 0;
	border: none;
	background: transparent;
	box-shadow: none;
}

.accordion .accordion-toggle {
	display: table;
	width: 100%;
	font-family: Oxygen;
	font-weight: bold;
	color: #65594d;
	padding: 13px 40px 13px 0;
	border: none;
	border-bottom: 1px solid #eeeeee;
	font-size: 16px;
	position: relative;
}

.accordion .accordion-toggle .indicator {
	position: absolute;
	right: 0;
	font-size: 10px;
	top: 50%;
	margin-top: -4px;
	color: #000;
}

.accordion .panel-body {
	padding: 20px 0 0;
}

.accordion .panel-body p {
	font-size: 15px;
	line-height: 24px;
	margin: 0 0 25px;
}

.accordion .panel-body p:last-child {
	margin: 0 0 5px;
}

/* =============================
   7. SUBPAGES
================================ */

.resources .ri-thumb {
	position: relative;
	cursor: pointer;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.resources .ri-thumb:after {
	content: "";
	background: rgba(73, 48, 23, 0.8);
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 5;
	opacity: 0;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.resources .ri-thumb h3 {
	position: absolute;
	color: #fff;
	font-weight: 600;
	width: 85%;
	max-width: 300px;
	background: rgba(73, 48, 23, 0.8);
	padding: 25px 15px;
	left: 0;
	right: 0;
	margin: 0 auto;
	top: 50%;
	transform: translateY(-50%);
	z-index: 11;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.resources .resource-item:hover .ri-thumb:after {
	opacity: 1;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.resources .resource-item:hover .ri-thumb h3 {
	background: rgba(73, 48, 23, 0);
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.resources .resource-item p {
	padding: 34px 0;
}

.resources .resource-item .btn {
	padding: 12px 38px;
}

