/* =============================
   10. GALLERY
================================ */

.gallery-carousel .slick-dots {
	margin-top: 30px;
}

.gallery-carousel .slick-dots li {
	float: left;
	margin: 0 30px 0 0;
	opacity: 0.3;
	cursor: pointer;
	position: relative;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.gallery-carousel .slick-dots li.slick-active {
	opacity: 1;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.gallery-carousel .slick-dots li:nth-child(2):after {
	content: "\f104";
	font-family: Flaticon;
	background: rgba(0, 0, 0, 0.6);
	position: absolute;
	z-index: 111;
	width: 35px;
	height: 35px;
	line-height: 35px;
	padding-left: 13px;
	color: #fff;
	font-size: 11px;
	border-radius: 50%;
	top: 50%;
	left: 50%;
	cursor: pointer;
	transform: translate(-50%, -50%);
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers {
	cursor: pointer;
}

.send-flowers .sf-thumb {
	border: 1px solid #e4e4e4;
	position: relative;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers .sf-thumb:after {
	content: "";
	background: #fff;
	opacity: 0.6;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: 100%;
	width: 100%;
	z-index: 5;
	opacity: 0;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers .sf-thumb i {
	background: #65594d;
	width: 65px;
	height: 65px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #fff;
	line-height: 65px;
	text-align: center;
	font-size: 17px;
	border-radius: 50%;
	z-index: 11;
	opacity: 0;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers:hover .sf-thumb:after {
	opacity: 0.6;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers:hover .sf-thumb i {
	opacity: 1;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.send-flowers h4 {
	font-size: 24px;
	color: #65594d;
	margin: 0 0 15px;
}

.send-flowers span {
	display: block;
	font-size: 24px;
	color: #65594d;
}

.send-flowers em {
	display: block;
	font-size: 14px;
	color: #65594d;
	margin: 20px 0 30px;
}

.send-flowers em i {
	font-size: 16px;
