.checkout-content form .rbox label {
	position: relative;
	cursor: pointer;
	padding-left: 3px;
}

.checkout-content form .rbox label:after {
	content: "";
	width: 11px;
	height: 11px;
	border: 2px solid #65594d;
	position: absolute;
	border-radius: 50%;
	left: -16px;
	top: 2px;
}

.checkout-content form .rbox label:before {
	content: "";
	width: 3px;
	height: 3px;
	background: #65594d;
	position: absolute;
	border-radius: 50%;
	left: -12px;
	top: 6px;
	opacity: 0;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

.checkout-content form input[type=checkbox]:checked + label:before,
.checkout-content form input[type=radio]:checked + label:before {
	opacity: 1;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

