	width: 12px;
	height: 12px;
	position: absolute;
	left: -16px;
	top: 0.5px;
	opacity: 1;
	color: #000;
	font-size: 9px;
	text-align: center;
	line-height: 12px;
	opacity: 0;
	-webkit-transition: 0.4s;
	-moz-transition: 0.4s;
	-ms-transition: 0.4s;
	-o-transition: 0.4s;
	transition: 0.4s;
}

