.promo {
	&__bg {
		padding: 65px 0 55px;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		text-align: right;
		@include md {
			padding: 32px 0 27px; } }
	&__text {
		padding-right: 65px;
		margin-bottom: 70px;
		@include md {
			padding-right: 32px;
			margin-bottom: 35px; }
		p {
			max-width: 475px;
			margin: 0 0 0 auto;
			font-size: 18px;
			line-height: 1.6;
			color: $brown;
			@include md {
				font-size: 12px;
 }				// background-color: rgba(255,255,255,.6)
			&:not(:last-child) {
				margin-bottom: 30px;
				@include md {
					margin-bottom: 15px; } } } }
	&__title {
		margin: 0 0 50px auto;
		max-width: 675px;
		font-family: $tahoma;
		font-size: 50px;
		@include md {
			margin: 0 0 24px auto;
			font-size: 20px; } }
	&__banner {
		position: relative;
		background-color: #cb4331;
		padding: 40px 44px 44px;
		border-radius: 10px 0 0 10px;
		max-width: 600px;
		margin: 0 0 0 auto;
		@include md {
			max-width: 420px;
			padding: 20px 22px 22px; }
		@include sm {
			max-width: 320px; }
		p {
			font-size: 14px;
			color: $white;
			@include md {
				font-size: 12px; } }
		&:after {
			position: absolute;
			content: '';
			left: -132px;
			top: 0;
			background-image: url(../img/candle-big.png);
			width: 175px;
			height: 235px;
			background-repeat: no-repeat;
			background-size: cover;
			background-position: 50% 50%;
			@include md {
				left: -80px;
				width: 100px;
				height: 150px; }
			@include xs {
				display: none; } } }
	&__banner-title {
		font-size: 55px;
		font-family: $tahoma;
		color: $white;
		margin-bottom: 6px;
		@include md {
			font-size: 35px; } } }
