.find-necrology {
    padding-top: 45px;
    background-color: $white_brown_bg;
    @include sm {
        padding-top: 15px; }
    &__head {
        margin: 0 auto;
        margin-bottom: 30px;
        max-width: 1030px;
        padding: 0 15px; }

    &__title {
        margin-bottom: 30px; }
    &__container {
        padding-bottom: 25%;
        background-size: contain;
        background-position: 50% 100%;
        background-repeat: no-repeat;
        @include sm {
            padding-bottom: 40%;
            display: flex;
            flex-direction: column;
            align-items: center; }
        a:not(:last-child) {
            margin-right: 30px;
            @include sm {
                margin-right: 0;
                margin-bottom: 15px; } } } }
