.key-questions {
    padding: 39px 0 68px;
    background-color: $white_brown_bg;
    &__container {}
    &__list {
        display: flex;
        flex-direction: column; }
    .section-head {
        margin-bottom: 33px;
        .title-l {
            margin-bottom: 9px; } } }

.key-item {
    position: relative;
    width: calc(50% + 85px);
    // float: left
    min-height: 170px;
    display: flex;
    justify-content: flex-end;
    &:before {
        position: absolute;
        content: "";
        top: 0;
        right: 84px;
        width: 2px;
        height: 100%;
        background-color: #7d7369;
        @include sm {
            display: none; } }
    &:nth-child(even) {
        &:before {
            right: auto;
            left: 84px; } }
    &:not(:last-child) {
        padding-bottom: 32px; }
    &:last-child {
        @include sm {
            margin-bottom: 9px; }
        &:before {
            display: none; } }
    @include sm {
        min-height: auto;
        width: 100%; }
    &:nth-child(even) {
        align-self: flex-end;
        justify-content: flex-start;
        @include sm;
        .key-item__body {
            padding-top: 12px;
            order: 2;
            // float: left
            text-align: left;
            @include sm {
                padding: 12px 0 0 0; } }
        .key-item__img {
            margin-left: 0;
            margin-right: 20px; } }
    &__body {
        padding-top: 12px;
        // float: left
        text-align: right;
        flex-grow: 3;
        max-width: 370px;
        @include sm {
            // width: 62%
            text-align: left;
            padding: 12px 0 0 0;
            max-width: none; } }
    &__wrapper {
        max-width: 470px; }
    &__title {
        font-size: 24px;
        margin-bottom: 20px;
        font-family: $tahoma;
        @include sm {
            margin-bottom: 10px;
            font-size: 16px; } }
    &__text {
        line-height: 1.6;
        margin-bottom: 25px;
        @include sm {
            margin-bottom: 15px;
            font-size: 14px;
            line-height: 1.2;
            letter-spacing: -0.02em; } }
    &__img {
        position: relative;
        // position: absolute
        // top: 0
        // left: 50%
        width: 170px;
        height: 170px;
        // transform: translateX(-50%)
        background-size: cover;
        background-position: 50% 50%;
        border-radius: 50%;
        margin-left: 20px;
        flex-shrink: 0;
        @include sm {
            margin-left: 12px;
            width: 112px;
            height: 112px; }
        &:before {} } }

