.map-wrap {
    padding: 20px 0 0;
    @include sm {
        padding: 0; }
    &__container {
        text-align: center; }
    &__title-l {
        margin-bottom: 17px; }
    &__select {
        max-width: 359px;
        margin: 0 auto 12px;
        .select {
            text-align: left;
            width: 100%; } } }

