body {
	position: relative;
	background: #FFF;
	line-height: 1.4;
	min-width: 320px;
	@include sm {
		font-size: 14px; }
	&.is-vip {
		&:before {
			position: fixed;
			content: "";
			top: 0;
			left: 0;
			bottom: 0;
			width: calc((100% - 1200px) / 2 + 288px);
			min-width: 243px;
			z-index: 1;
			background-color: #e6ddd0;
			@include r(1049) {
				display: none; } } } }

.inner {
	@include sm {
		padding: 0 25px; } }

.title-l {
	font-weight: 600;
	font-size: 45px;
	line-height: 1.2;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		line-height: 1;
		font-size: 25px; }
	&_mod {
		font-weight: 600;
		font-size: 45px;
		line-height: 1.2;
		font-family: $cormorant_infant;
		color: $white;
		@include sm {
			font-size: 30px; } }
	@include sm {
		font-size: 25px; }
	&_smaller {
		font-size: 40px; } }

.title-m {
	font-weight: 600;
	font-size: 30px;
	line-height: 1.2;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		line-height: 1;
		font-size: 20px!important; } }

.title-s {
	font-weight: 700;
	font-size: 24px;
	line-height: 1.2;
	font-family: $cormorant_infant;
	color: $brown;
	@include sm {
		font-family: $tahoma;
		font-size: 16px!important; }
	&_mod {
		font-weight: 700;
		font-size: 24px;
		line-height: 1.2;
		font-family: $cormorant_infant;
		color: $brown;
		@include sm {
			font-size: 21px; } } }

.title-sm {
	font-family: $cormorant_infant;
	@include f(20px, 700, 1.2);
	text-transform: uppercase; }

.line {
	height: 1px;
	width: 100%;
	background-color: rgba(#ffffff,.5);
	margin-bottom: 20px;
	@include sm {
		background-color: rgba(#d9c8ab,.2); } }

.md-hide {
	@include r(1049) {
		display: none !important; } }

.mob-hide {
	@include sm {
		display: none !important; } }

.page_head {
	border-bottom: 1px solid rgba(#f1ede8,.5); }

.hidein {
	@include sm {
		display: none; } }

.showin {
	display: none;
	@include sm {
		display: block; } }

.hidein_md {
	@include md {
		display: none; } }

.bcrumbs {
	font-family: $tahoma;
	font-weight: 400;
	font-size: 14px;
	color: $brown; }

.side-form .input-checkbox {
	margin-bottom: 0; }

.side-form .cbox label:after {
	top: 3px;
	width: 14px;
	height: 14px;
	background: repeating-linear-gradient(to bottom right,rgb(192, 192, 192) 0%,rgb(255, 255, 255) 100%);
	background: repeating-linear-gradient(top left,rgb(192, 192, 192) 0%,rgb(255, 255, 255) 100%);
	z-index: 1; }

.side-form .cbox label:before {
	top: 4px;
	left: -15px;
	width: 12px;
	height: 12px;
	content: '';
	border: 1px solid #ccc;
	z-index: 2;
	background-color: $brown; }

.padding-aside {
	@include sm {
		padding-top: 22px; } }

.banner {
	width: 100%;
	display: block;
	img {
		max-width: 100%;
		display: block; } }

.ratings {
	.ratings__appraisal {
		top: 0;
		margin-left: 10px;
		color: #65594d;
		font-size: 16px;
		font-weight: 700;
		@include sm {
			margin-left: 3px;
			font-size: 11px; } }
	span, .fi {
		@include sm {
			font-size: 10px; } } }

.image {
	margin: 55px 0;
	@include md {
		margin: 30px 0; }
	@include sm {
		margin: 20px 0; }
	img {
		max-width: 100%;
		display: block; } }

.row {
	&_rel {
		position: relative;
		z-index: 2; } }
