.commemorate {
    padding-top: 50px;
    @include sm {
        padding-top: 15px; }
    &__title {}
    &__head {
        margin-bottom: 30px;
        padding: 0 15px; }
    &__text {
        margin: 0 auto;
        max-width: 780px;
        margin-bottom: 40px;
        padding: 0 15px; }
    &__container {
        padding-bottom: 37%;
        background-size: contain;
        background-position: 50% 100%;
        background-repeat: no-repeat;
        @include md {
            padding-bottom: 40%; } } }
