.goods_services {
	padding-top: 65px;
	@include sm {
		padding-top: 13px; }
	.shop-single-info {
		@include sm {
			padding-top: 15px; } }
	.shop-carousel-wrap {
		position: relative;
		cursor: pointer;
		.zoom-img {
			position: absolute;
			display: block;
			top: 50px;
			right: 41px;
			transform: translate(50%,-50%);
			z-index: 10;
			opacity: 0;
			transition: opacity .3s;
			.icon {
				font-size: 52px;
				fill: $gray_brown;
				@include sm {
					font-size: 32px; }
				@include md {
					font-size: 32px; } }
			@include sm {
				top: 35px;
				right: 105px; } }
		&:hover {
			.zoom-img {
				opacity: 1; } } }
	.shop-carousel {
		.slick-list {
			@include sm {
				margin-right: 70px; } }
		position: relative;
		a {
			display: block; } }
	.item {
		border: 2px solid $gray_brown;
		img {
			width: 100%; } }
	.slick-dots {
		@include sm {
			position: absolute;
			right: -105px;
			bottom: 92px;
			transform: rotate(-90deg); }
		li {
			border: 1px solid $gray_brown;
			transition: box-shadow .3s;
			&:hover {
				box-shadow: 0 0 14px rgba(0,0,0,.2); }
			@include sm {
				transform: rotate(90deg); } } }

	.prev8,
	.next8 {
		transition: color .3s;
		@include sm {
			opacity: 0; }
		&:hover {
			color: $brown_orange; } }
	.next8 {
		@include sm {
			right: 95px; } } }

.item.slick-slide.slick-current.slick-active {
	outline: none; }


.section-head {
	&__title {
		margin-bottom: 38px;
		@include sm {
			margin-bottom: 10px; } } }


.facilities .team-info {
	padding-bottom: 0; }


.services-block {
	aside {
		@include md {
			margin-bottom: 21px; } }
	&:not(:last-child) {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 32px; } }
	.tab-pane {
		font-size: 0;
		padding: 0;
		@include sm {
			border: none; }
		.team-info {
			padding: 25px 40px;
			margin-bottom: 0;
			cursor: pointer;
			transition: box-shadow .3s;
			@include sm {
				padding: 25px 0; }
			&:not(:last-child) {
				border-bottom: 1px solid #eaeaea; }
			&:hover {
				box-shadow: 0 0 14px rgba(0,0,0,.2); }
			.posts-image {
				margin-left: 15px; } } } }

.facilities .team-info .social {
	top: 25px;
	right: 40px;
	z-index: 2;
	@include sm {
		display: none; }
	a.social__link {
		background-color: $white_brown_bg;
		color: $brown_orange;
		&:hover {
			background-color: $brown_orange;
			color: $white_brown_bg;
			.icon {
				fill: $white_brown_bg; } } } }


.staff {
	font-size: 15px;
	color: $brown;
	&__title-block {
		margin-bottom: 15px;
		@include md {
			padding-left: 100px;
			margin-bottom: 30px; }
		@include sm {
			padding-left: 0; } }
	&__title {
		font-family: $cormorant_infant;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 18px;
		color: $brown;
		display: block;
		margin-bottom: 5px;
		text-align: left; }
	&__description {
		font-style: italic;
		font-size: 15px;
		color: #8e857a; }
	p {
		margin-bottom: 15px;
		i.flaticon-black {
			color: $brown; }
		&:last-child {
			margin-bottom: 0;
			color: #8e857a; } } }

.team-info {
	label {
		font-size: 15px;
		color: $brown;
		margin-bottom: 10px;
		@include sm {
			font-size: 14px; } }
	p {
		@include sm {
			margin-bottom: 14px;
			font-size: 14px; } }
	.post-form__input,
	.post-form__textarea {
		margin-bottom: 10px; }
	.post-form__col {
		@include sm {
			margin-bottom: 0; } }
	.post-form__btn {
		text-align: right;
		@include sm {
			text-align: left; } } }

.services-block .team-info {
	border-bottom: none;
	margin-bottom: 0; }

.team-info_mod .post-form__textarea {
	margin-bottom: 25px; }

.companies-item__type {
	width: 100%;
	.product-prices {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		@include sm {
			flex-wrap: wrap; } }
	.product-single-price {
		font-size: 24px;
		margin: 0;
		span {
			margin-left: 8px;
			font-size: 16px; }
		&:last-child {
			max-width: 85px;
			font-size: 0;
			@include sm {
				max-width: 64px; } }
		img {
			max-width: 100%; } } }

.services-agency {
	.wind__agency {
		margin-bottom: 25px; }
	.agency__table {
		border: 1px solid #eaeaea; } }

.product-single-price {
	@include sm {
		font-size: 36px; }
	span {
		@include sm {
			margin-left: 30px;
			font-size: 18px; } } }

.services-block {
	.mob-show {
		display: none;
		@include sm {
			display: block; } }
	.mob-hide {
		@include sm {
			display: none; } }
	.agency {
		border: none;
		font-size: 14px;
		p {
			@include sm {
				font-size: 14px;
				color: $brown;
				margin-bottom: 20px;
				&:last-child {
					margin-bottom: 0; } } }
		&:hover {
			box-shadow: none; }
		&__title {
			margin: 10px 0 15px;
			text-align: center;
			text-transform: uppercase; }
		&__head {
			border: none;
			@include md {
				padding-right: 0;
				padding-left: 0; }
			@include sm {
				border: 1px solid #eaeaea;
				margin-bottom: 20px; } }
		&__logo {
			float: none;
			max-width: 100%;
			.row {
				@include sm {
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-right: 0;
					margin-left: 0; } }
			img {
				max-width: 100%;
				max-height: 100px;
				@include sm {
					width: 128px; } } }
		&__link {
			float: none;
			text-align: center;
			margin: 0; }
		&__contacts {
			@include sm {
				margin-top: 0; } }
		.visitors {
			padding-top: 18px;
			padding-bottom: 10px;
			.counter__item {
				font-size: 14px; } }
		.visitors_mod {
			padding-top: 0;
			border: none;
			margin-bottom: 40px; } }
	.contacts-item {
		&__icon {
			margin-top: 3px;
			margin-right: 3px;
			font-size: 12px; }
		&.padding {
			padding-left: 17px; } } }

.services-block {
	.map {
		max-height: 398px; } }

.services-block {
	.all-companies__item {
		margin-bottom: 30px;
		margin-left: -10px;
		margin-right: -10px; }
	.companies-item__text,
	.companies-item__wrap {
		width: 100%; }
	.companies-item__name {
		margin-bottom: 8px; }
	.all-companies__tile {
		margin-bottom: 0; }
	.companies-item__type {
		margin-bottom: 10px; }
	.sf-thumb {
		margin-bottom: 13px;
		@include sm {
			height: 130px;
			line-height: 130px;
			img {
				max-width: 100%; } } }
	.page-nav {
		border-top: 1px solid #eaeaea;
		padding: 32px 0 51px;
		@include sm {
			border: none;
			padding: 0; } }
	.side-widget {
		margin-top: 42px;
		&.hidein {
			@include md {
				display: none; } } }
	.bottom-image {
		img {
			margin-top: -100px; } }
	&.margin-bot {
		margin-bottom: 0; } }
