.aside {
	position: relative;
	will-change: min-height;
	@include md {
		position: absolute; }
	&__container {
		position: relative;
		padding-right: 20px;
		transform: translate(0, 0);
		transform: translate3d(0, 0, 0);
		will-change: position, transform; }
	&__item {
		padding: 16px 0;
		&:not(:last-child) {
			border-bottom: 1px solid #DAD1C6; } } }
.js-aside-container {
	position: relative; }
