.services-main-list {
    padding: 25px 0 17.9%;
    background-size: contain;
    background-position: 50% 100%;
    background-repeat: no-repeat;
    &__container {}
    &__head {
        margin-bottom: 35px; }
    &__title {}
    &__list {
        display: flex;
        flex-direction: column;
        .key-item {
            @include sm {
                align-items: center; }
            &__body {
                @include sm {
                    padding: 0; } }
            &__title {
                margin-bottom: 10px;
                @include sm {
                    font-weight: 700;
                    margin-bottom: 0; } }
            &__text {
                @include sm {
                    margin-bottom: 0; } }
            &__img {
                @include sm {
                    width: 65px;
                    height: 65px; } }
            .btn {
                @include sm {
                    display: none; } } } } }

