.page-head {
	padding: 20px 0 13px;
	border-bottom: 1px solid rgba(#f1ede8,.5);
	@include sm {
		padding: 20px 0 7px; }
	a {
		color: $brown_orange;
		&:hover {
			color: #cecece;
			.icon-house {
				font-size: 16px;
				fill: #cecece; } } }
	&__container {
		@include clr; }
	&__bcrumbs {
		float: left;
		li {
			position: relative;
			float: left;
			&:not(:last-child) {
				margin-right: 34px;
				&:after {
					font-family: Flaticon;
					content: "\f101";
					// color: #cecece
					position: absolute;
					font-size: 8px;
					top: 5px;
					right: -20px; } } } } }


.grief {
	padding-top: 25px;
	@include sm {
		padding-top: 11px; }
	&__container {}
	&__head {}
	&__title {
		margin-bottom: 11px; }
	&__description {}
	&__body {
		display: flex;
		justify-content: space-between;
		@include sm {
			flex-direction: column; } }
	&__text {
		padding: 24px 50px 0 0;
		width: 40%;
		line-height: 1.6;
		@include sm {
			width: 100%;
			font-size: 14px;
			line-height: 1.4;
			padding: 11px 0 0; }
		p {
			&:not(:last-child) {
				margin-bottom: 24px;
				@include sm {
					margin-bottom: 0; } } }
		&_reverse {
			padding: 24px 0 0 50px;
			p:last-child {
				margin-bottom: 50px; } } }
	&__img {
		// width: 65%
		align-self: flex-end;
		img {
			display: block;
			width: 100%; } }
	&__footer {
		margin: 50px 0;
		@include sm {
			margin: 30px 0;
			@include clr;
			a {
				float: right; } } } }

.grief-2 {
	&__container {}
	&__head {}
	&__title {
		@include sm {
			margin-bottom: 21px; } }
	&__body {
		@include sm {
			position: relative;
			flex-direction: row; } }
	&__img {
		@include sm {
			margin-right: 10px;
			align-self: flex-start; } }
	&__text {
		@include sm {
			padding: 0; } } }

.narrow-paragraph {
	max-width: 880px;
	margin: 0 auto;
	line-height: 1.6; }

.bg-high {
	background-size: cover;
	background-position: 50% 50%;
	background-repeat: no-repeat;
	@include sm {
		background: none; } }

.cemetery-selection {
	padding: 54px 0 0 0;
	background-size: auto;
	background-position: 50% 0;
	background-color: #fff;
	background-repeat: no-repeat;
	color: $white_brown_bg;
	@include sm {
		padding-top: 21px;
		background-position: 70% 25%; }
	&__container {}
	&__head {
		margin-bottom: 17px; }
	&__title {
		margin: 0 auto 18px;
		max-width: 430px;
		line-height: 0.9;
		color: $white_brown_bg;
		@include sm {
			max-width: 300px;
			margin-bottom: 23px; } }
	&__description {
		line-height: 0.9; }
	&__body {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 25px; } }
	&__text {
		max-width: 880px;
		margin: 0 auto;
		margin-bottom: 54px;
		line-height: 1.6;
		@include sm {
			line-height: 1.4;
			font-size: 14px;
			margin-bottom: 22px; } }
	&__map {
		max-width: 975px;
		margin: 0 auto;
		background-color: #fff;
		display: flex;
		padding: 20px;
		@include md {
			flex-wrap: wrap; } }
	&__col-left {
		flex: 30%;
		@include md {
			flex: 100%; } }
	&__col-right {
		margin-left: 30px;
		flex: 70%;
		@include md {
			margin-left: 0;
			flex: 100%; } }
	&__img-map {
		width: 100%;
		height: 0;
		padding-bottom: 71%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: 50% 50%;
		min-height: 460px;
		@include md {
			padding-bottom: 50%; } } }

.full-services {
	padding-top: 73px;
	background-size: contain;
	background-position: 50% 0;
	background-color: #fff;
	background-repeat: no-repeat;
	@include sm {
		padding-top: 15px; }
	&__container {}
	&__head {
		margin-bottom: 38px;
		@include sm {
			margin-bottom: 0; } }
	&__title {
		margin: 0 auto 18px;
		max-width: 430px;
		line-height: 0.9; }
	&__description {
		line-height: 0.9; }
	&__body {
		margin-bottom: -20px; }
	&__text {
		margin-bottom: 54px; }
	&__img {
		padding-bottom: 32px;
		background-color: $white_brown_bg;
		img {
			position: relative;
			display: block;
			width: 100%;
			z-index: 3; } } }


.find-church {
	padding: 76px 0 48px;
	@include sm {
		padding: 16px 0 31px; }
	&__container {}
	&__body {
		display: flex;
		justify-content: space-between;
		@include sm {
			flex-direction: column;
			padding-left: 28%; } }
	&__title {
		width: 17%;
		line-height: 1.1;
		@include sm {
			width: 100%;
			font-size: 21px;
			margin-bottom: 17px; } }
	&__img {
		position: absolute;
		// top: -250px
		bottom: -23px;
		right: 38%;
		@include sm {
			width: 130px;
			left: -145px;
			bottom: auto;
			top: -15px; }
		img {
			display: block;
			width: 100%; } }
	&__text {
		position: relative;
		// width: 85%
		padding: 14px 0 0 54.5%;
		@include sm {
			padding: 0;
			font-size: 14px;
			display: flex;
			flex-direction: column;
			p {
				max-width: none;
				line-height: 1.4;
				margin-bottom: 19px; } } } }

.text-plus-button {
	p {
		max-width: 370px;
		line-height: 1.6;
		margin-bottom: 64px; } }

.find-hall {
	padding: 56px 0 206px;
	background-size: cover;
	background-position: 70% 50%;
	background-repeat: no-repeat;
	@include sm {
		padding: 23px 0 34px; }
	&__container {}
	&__body {
		// columns: 2
		// column-gap: 30px
		width: 67.5%;
		display: flex;
		@include sm {
			flex-direction: column; } }
	&__title {
		max-width: 400px;
		line-height: 0.9;
		margin-bottom: 68px;
		@include sm {
			font-size: 21px;
			max-width: 200px;
			line-height: 1.15;
			margin-bottom: 8px; } }
	&__text {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include sm {
			p {
				line-height: 1.3;
				margin-bottom: 19px; } }
		&:not(:last-child) {
			margin-right: 30px;
			@include sm {
				margin: 0 0 13px 0; } }
		a {
			align-self: flex-start; } } }

.one-place {
	padding: 28px 0 0;
	background-color: $white_brown_bg;
	overflow: hidden;
	@include sm {
		padding-top: 23px; }
	&__container {}
	&__head {
		margin-bottom: 24px;
		@include sm {
			margin-bottom: 13px; } }
	&__body {
		margin-bottom: 40px;
		@include sm {
			margin-bottom: 32px; } }
	&__title {
		margin-bottom: 11px;
		@include sm {
			font-size: 21px;
			margin-bottom: 0; } }
	&__text {
		margin-bottom: 22px;
		@include sm {
			font-size: 14px;
			line-height: 1.4;
			margin-bottom: 18px; } }
	&__img {
		margin: 0 auto;
		width: 55%;
		box-shadow: 0 3px 7px 0 rgba(0,0,0,.35);
		@include sm {
			width: 83%; }
		img {
			display: block;
			width: 100%; } } }

.companies {
	padding: 44px 0 0 0;
	@include sm {
		padding-top: 15px; }
	&__container {}
	&__title {
		margin-bottom: 17px;
		@include sm {
			font-size: 21px; } }
	&__cities {
		margin-bottom: 15px; } }


.select-primary {
	position: relative;
	display: inline-block;
	vertical-align: top;
	&:before {
		position: absolute;
		content: '';
		top: 50%;
		right: 20px;
		transform: translateY(-50%);
		@include arr(10, 5, #000,b); }
	select {
		// background: url(../img/grief/icons8-sort-down-filled-100.png) no-repeat 95% #f5f1ec
		background-color: #f5f1ec;
		background-size: 15px;
		border: none;
		font-size: 16px;
		padding: 14px 40px 12px 20px;
		border-radius: 5px;
		width: 357px;
		appearance: none;
		cursor: pointer; } }


