.shop-single-info {
	.mob-show {
		display: none;
		@include sm {
			display: block; } }
	.mob-hide {
		@include sm {
			display: none; } }
	.product-prices {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 30px; }
	.product-single-price {
		color: $brown;
		margin: 0;
		@include sm {
			font-size: 36px; }
		span {
			color: #bbb3a1;
			margin-left: 42px;
			@include sm {
				margin-left: 15px; } }
		img {
			display: block;
			@include sm {
				max-width: 120px; } } }
	p {
		font-size: 14px;
		color: $brown;
		margin-bottom: 30px; }
	.product-info {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		margin-bottom: 15px;
		@include sm {
			span {
				margin-right: 0; } } }
	.price-btn {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include sm {
			justify-content: center;
			margin-bottom: 22px; } } }

.pr-ratings {
	margin-right: 0;
	&__left {
		font-size: 15px;
		font-weight: 600;
		margin-left: 6px;
		@include sm {
			margin-left: 0;
			font-size: 13px; } }
	&__right {
		font-weight: 400; } }


.product-reviews {
	&__link {
		text-decoration: none;
		margin-right: 15px;
		color: #bbb3a1;
		white-space: nowrap;
		@include sm {
			margin-right: 0;
			font-size: 13px; }
		i {
			color: $brown;
			margin-right: 8px; } } }

.price-list {
	color: $brown;
	font-size: 14px;
	margin-bottom: 58px;
	@include sm {
		margin-bottom: 0; }
	span {
		display: block;
		margin-bottom: 8px; }
	&__item {
		position: relative;
		padding-left: 10px;
		margin-bottom: 10px;
		&:last-child {
			@include sm {
				display: none; } }
		&:after {
			position: absolute;
			top: 11px;
			left: 0;
			transform: translate(0,-50% );
			content: '';
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: $brown; } } }
