.schedule {
	padding: 38px 0;
	margin-bottom: 35px;
	border-bottom: 1px solid #eee;
	&__title {
		text-align: center;
		margin-bottom: 25px;
		font-size: 36px; }
	&__wrap {
		position: relative;
		width: 100%;
		&:hover {
			.aside-block {
				z-index: 3;
				opacity: 1; } } }
	&__block {
		width: 100%;
		img {
			padding: 50px 25px;
			max-width: 100%;
			display: block;
			border: 1px solid #e6ddd0; } } }

.aside-block {
	background-color: $white;
	transition: opacity .3s;
	opacity: 0;
	z-index: -1;
	color: $brown;
	&__wrap {
		position: absolute;
		padding: 40px 28px;
		width: 300px;
		left: -300px;
		top: 0;
		background-color: $white; }
	&__title {
		font-family: $tahoma;
		margin-bottom: 35px;
		font-weight: 600;
		text-transform: uppercase; }
	&__item {
		font-size: 14px;
		position: relative;
		padding-left: 33px;
		&:not(:last-child) {
			margin-bottom: 25px; }
		&.beige {
			.icon {
				fill: #c7bfb7; } }
		&.brown {
			.icon {
				fill: #65594d; } }
		&.yellow {
			.icon {
				fill: #f3daa0; } }
		&.red {
			.icon {
				fill: #e44b5a; } }
		&.purpule {
			.icon {
				fill: #ab8bb1; } }
		&.gray {
			.icon {
				fill: #959595; } } }
	&__icon {
		position: absolute;
		top: 2px;
		left: 0; } }

