.wind {
	p {
		&:not(:last-child) {
			margin-bottom: 18px; } }
	&__bg {
		padding-bottom: 50%;
		background-position: 50% 50%;
		background-size: cover;
		background-repeat: no-repeat; }
	&__preson {
		@include md {
			margin-bottom: 30px; } }
	&__agency {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	&__description {
		margin-bottom: 28px;
		line-height: 1.6;
		text-align: center;
		@include sm {
			text-align: left; }
		p {
			&:not(:last-child) {
				margin-bottom: 0; } } }
	.facilities .team-info {
		.col-sm-3 {
			@media only screen and (max-width: 1050px) and (min-width: 768px) {
				width: 25%; } }
		.col-sm-9 {
			@media only screen and (max-width: 1050px) and (min-width: 768px) {
				width: 75%; } }
		.col-xs-12 {
			@media only screen and (max-width: 767px) and (min-width: 250px) {
				width: 100%; } } } }
.wind-list {
	&:not(:last-child) {
		margin-bottom: 18px; }
	li {
		position: relative;
		padding-left: 10px;
		display: block;
		&:before {
			position: absolute;
			content: "";
			top: 8px;
			left: 0;
			height: 5px;
			width: 5px;
			border-radius: 50%;
			background-color: #65594d; }
		&:not(:last-child) {
				margin-bottom: 18px; } } }
.wind-head {
	margin-bottom: 34px;
	padding-bottom: 2px;
	text-align: right;
	border-bottom: 1px solid #F3EEE8;
	@include sm {
		display: none; }
	&__title,
	&__name {
		vertical-align: top;
		display: inline-block; }
	&__title {
		color: #65594d;
		font-family: $cormorant_infant;
		font-size: 20px;
		font-weight: 700; }
	&__name {
		position: relative;
		color: #d4c8b8;
		font-family: $tahoma;
		font-size: 15px;
		font-weight: 400;
		line-height: 1.9;
		&:not(:first-child) {
			padding-left: 15px;
			margin-left: 6px; }
		&:before {
			position: absolute;
			content: "";
			top: 50%;
			left: 0;
			margin-top: -2px;
			width: 4px;
			height: 4px;
			border-radius: 50%;
			background-color: #65594d; } } }
.read-all {
	text-align: right;
	&__link {
		color: #605449;
		font-size: 14px;
		font-weight: 400;
		border-bottom: 1px dashed;
		&:hover {
			border-color: transparent; }
		&.is-open {
			.flaticon-arrows {
				@include rotate(-90); } }
		.flaticon-arrows {
			margin-left: 10px;
			font-size: 12px;
			@include rotate(90);
			transition: transform .3s;
			display: inline-block; } } }
.about-agency {
	&:not(:last-child) {
		margin-bottom: 35px; }
	p {
		line-height: 1.6;
		&:not(:last-child) {
			margin-bottom: 26px;
			@include md {
				margin-bottom: 18px; } } }
	.ratings {
		padding: 0; }
	&__info {
		padding: 25px;
		color: #65594d;
		font-size: 16px;
		font-weight: 400;
		border: 1px solid #f3eee8;
		@include sm {
			padding: 8px 15px; }
		p {
			&:not(:last-child) {
				margin-bottom: 10px;
				@include sm {
					margin-bottom: 3px; } } } }
	&__info-wrap {
		margin: 0 auto;
		max-width: 127px;
		text-align: center;
		@include sm {
			display: table;
			max-width: 100%;
			width: 100%; } }
	&__logo {
		margin-bottom: 10px;
		max-width: 271px;
		vertical-align: top;
		display: inline-block;
		@include sm {
			max-width: 130px;
			display: block; }
		img {
			max-width: 100%;
			display: block; } }
	&__head {
		margin-bottom: 25px;
		@include sm {
			margin-bottom: 0;
			text-align: left; } }
	&__contact,
	&__head {
		@include sm {
			width: 50%;
			vertical-align: top;
			display: table-cell; } }
	&__contact {
		@include sm {
			vertical-align: middle; } }
	&__title-s {
		text-transform: uppercase;
		margin-bottom: 30px;
		@include md {
			margin-bottom: 18px; } }
	&__col {
		&:not(:last-child) {
			@include md {
				margin-bottom: 25px; } } } }
.links a {
	@include sm {
		padding-left: 20px; }
	&:last-child {
		@include sm {
			margin-bottom: 20px; } } }

.person {
	color: #605449;
	text-align: center;
	line-height: 1.6;
	display: block;
	&:hover {
		.person {
			&__img {
				opacity: 0.32; }
			&__name {
				border-color: currentColor; } } }
	&__img {
		margin: 0 auto 25px;
		height: 127px;
		width: 127px;
		border-radius: 50%;
		box-shadow: 0 0 0 1px #E6DDD0;
		border: 5px solid $white;
		transition: opacity .4s;
		background-position: 50% 50%;
		background-size: cover;
		background-repeat: no-repeat;
		@include sm {
			height: 107px;
			width: 107px;
			margin-bottom: 10px; } }
	&__name-wrap {
		margin-bottom: 18px;
		@include sm {
			margin-bottom: 0; } }
	&__name {
		font-family: $tahoma;
		font-weight: 400;
		color: #605449;
		line-height: 1.2;
		font-size: 20px;
		transition: border-color .4s;
		text-transform: uppercase;
		border-bottom: 1px solid transparent;
		display: inline;
		@include sm {
			font-size: 18px; } }
	&__position {
		margin-bottom: 18px;
		color: #c3b8a8;
		font-size: 15px;
		@include sm {
			margin-bottom: 0;
			font-size: 14px; } } }
